.hr {
    animation-duration: 1250ms;
    animation-iteration-count: 1;
    height: 170px;
    transform-origin: bottom;
}

.jump {
    animation-name: jump;
    animation-timing-function: cubic-bezier(0.280, 0.840, 0.420, 1);
}

.Tooltip {
     overflow-x: auto;
 }

@keyframes jump {
    0%   { transform: translateY(0); }
    10%  { transform: translateY(0); }
    30%  { transform: translateY(-180px); }
    50%  { transform: translateY(0px); }
    57%  { transform: translateY(-7px); }
    64%  { transform: translateY(0); }
    100% { transform: translateY(0); }
}

@font-face {
    font-family: Font;
    src: url(../font.otf);
}

.score_text {
    font-family: Font;
    font-size: 56px;
    font-weight: 800;
    letter-spacing: 8px;
    vertical-align: middle;
}

.scores {
    font-family: Font;
    font-size: 22px;
    font-weight: 400;
    display: flex;
    align-items: center;
    justify-content: center;
}
.score_right {
    margin-left: auto;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.back_blur {
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
}

.Card__in {
    height: 100%;
}

.gradient_light {
    background-image: linear-gradient(180deg,hsla(0,0%,96.1%,0),hsla(0,0%,96.1%,.02) 9%,hsla(0,0%,96.1%,.06) 19%,hsla(0,0%,96.1%,.12) 28%,hsla(0,0%,96.1%,.2) 38%,hsla(0,0%,96.1%,.29) 48%,hsla(0,0%,96.1%,.39) 57%,hsla(0,0%,96.1%,.5) 66%,hsla(0,0%,96.1%,.61) 74%,hsla(0,0%,96.1%,.71) 81%,hsla(0,0%,96.1%,.8) 88%,hsla(0,0%,96.1%,.88) 93%,hsla(0,0%,96.1%,.95) 97%,hsla(0,0%,96.1%,.98) 99%,#f5f5f5);
}
.gradient_dark {
    background-image: linear-gradient(to top,#232324,rgba(35,35,36,.98),rgba(35,35,36,.94),rgba(35,35,36,.88),rgba(35,35,36,.8),rgba(35,35,36,.71),rgba(35,35,36,.61),rgba(35,35,36,.5),rgba(35,35,36,.39),rgba(35,35,36,.29),rgba(35,35,36,.2),rgba(35,35,36,.12),rgba(35,35,36,.06),rgba(35,35,36,.02),rgba(35,35,36,0));
}

.font {
    font-family: Font;
}

.centered {
    display: flex;
    justify-content: center;
    align-items: center;
}

.event_score_text {
    margin: 8px;
    font-size: 12px;
    opacity: 0.5;
}

.light_outline {
    filter: drop-shadow(8px 0px 0 white) drop-shadow(0px 8px 0 white) drop-shadow(-8px 0px 0 white) drop-shadow(0px -8px 0 white);
}

.flex_end {
    position: absolute;
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

