.top {
    opacity: 0;
    background-color: white;
    position: absolute;
    width: 100vw;
    padding: 12px;
}

.top_show {
    opacity: 1;
    animation: show_top 250ms ease-in-out;
}

.top_close {
    opacity: 0;
    animation: close_top 250ms ease-in-out;
}

@keyframes show_top {
    from {
        opacity: 0;
        transform: translateY(-100%);
    }

    to {
        opacity: 1;
        transform: translteY(0);
    }
}

@keyframes close_top {
    to {
        opacity: 0;
        transform: translateY(-100%);
    }

    from {
        opacity: 1;
        transform: translteY(0);
    }
}

.other {
    background-color: black;
    opacity: 0;
    position: absolute;
    width: 100vw;
    height: 100vh;
}

.other_show {
    opacity: 0.5;
    animation: show_other 250ms ease-in-out;
}

.other_close {
    opacity: 0;
    animation: close_other 250ms ease-in-out;
}

@keyframes show_other {
    from {
        opacity: 0;
    }

    to {
        opacity: 0.5;
    }
}

@keyframes close_other {
    to {
        opacity: 0;
    }

    from {
        opacity: 0.5;
    }
}