#chest_shape {
    opacity: 0;
}

@keyframes chest-shape {
    0% { opacity: 0.8; transform: scale(0, 0); }
    60% { opacity: 0.8; transform: scale(2.25, 2.25); }
    100% { opacity: 0; transform: scale(2.25, 2.25); }
}

@keyframes chest-icon {
    0% { transform: translate(-47%, -52%) scale(1, 1); }
    25% { transform: translate(-51%, -49%) scale(0.25, 0.25); }
    50% { transform: translate(-45%, -54%) scale(0.5, 0.5); }
    75% { transform: translate(-52%, -43%) scale(0.75, 0.75); }
    100% { transform: translate(-50%, -50%) scale(1, 1); }
}

@keyframes chest-drop {
    0% { transform: translate(-50%, -50%) scale(0, 0); }
    50% { transform: translate(-50%, -50%) scale(1.5, 1.5); }
    100% { transform: translate(-50%, -50%) scale(1, 1); }
}

.chest_shape_animation {
    animation: chest-shape 400ms ease-in-out;
}

.drop_text {
    color: white;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -60%);
    animation: chest-drop-text 250ms ease-in-out;
}

@keyframes chest-drop-text {
    0% { transform: translate(-50%, 0%); opacity: 0; }
    100% { transform: translate(-50%, -60%); opacity: 1; }
}

.invisible {
    position: absolute;
    visibility: hidden;
}
.invisible_2 {
    opacity: 0;
}

.flash {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100vw;
    height: 100vh;
    opacity: 0;
    background: white;
    animation: flashing 250ms ease-in-out;
}

.flash2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100vw;
    height: 100vh;
    opacity: 0;
    background: white;
    animation: flashing 1500ms ease-in-out;
}

@keyframes flashing {
    0% { opacity: 0.1; }
    65% { opacity: 1.0; }
    100% { opacity: 0; }
}

.chest_icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.chest_drop {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: chest-drop 500ms ease-in-out;
}

.chest_icon_animation {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: chest-icon 400ms ease-in-out;
}

.chest_shapes {
    opacity: 0;
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    width: 15px; height: 15px;
    transform: rotate(45deg);
    box-shadow:
            -200px -125px white,-250px 0px white,-200px 125px white,200px -125px white,250px 0px white,200px 125px white,-125px -200px white,0px -250px white,125px -200px white,-125px 200px white,0px 250px white,125px 200px white;
    animation: 500ms bang ease-out;
}

.chest_shapes2 {
    opacity: 0;
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    width: 15px; height: 15px;
    box-shadow:
            -200px -125px white,-250px 0px white,-200px 125px white,200px -125px white,250px 0px white,200px 125px white,-125px -200px white,0px -250px white,125px -200px white,-125px 200px white,0px 250px white,125px 200px white;
    animation: 500ms bang ease-out;
}

@keyframes bang {
    from {
        box-shadow:
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white,
                0 0 white;
    }
    0% { opacity: 0.4; }
    60% { opacity: 0.8; }
    80% { opacity: 0.2; }
    100% { opacity: 0; }
}

#open_chest_container {
    font-family: Font;
    font-size: 18px;
    height: 100vh;
    width: 100vw;
    background: rgba(0,0,0,0.6);
    display: flex;
    align-items: center;
    justify-content: center;
}

.dark_popout_container{
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.8);
}

.end_game_drop_container {
    position: absolute;
    top: 45%; left: 50%;
    transform: translate(-50%, -50%);
    width: 125px; height: 125px;
    background: rgba(44,45,46,0.8);
    border-radius: 25%;
}

.end_game_buttons_container {
    position: absolute;
    top: 70%; left: 50%;
    transform: translate(-50%, -50%);
}

.end_game_title {
    font-family: Font;
    font-size: 30px;
    font-weight: 800;
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -60%);
    animation: chest-drop-text 250ms ease-in-out;
}

.end_game_drop_img {
    position: absolute;
    top: 45%; left: 50%;
    transform: translate(-50%, -50%);
}

.end_game_drop_text_info {
    font-family: Font;
    text-align: center;
    font-size: 14px;
    color: white;
    position: absolute;
    top: 65%; left: 50%;
    transform: translate(-50%, -50%);
}

.avatars {
    font-family: Font;
    text-align: center;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10vh 15vw;
}

.avatar_right {
    margin-left: auto;
}

.text-prefight {
    font-family: Font;
    text-align: center;
    font-size: 40px;
    color: white;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
}

.fight-timeout-hint {
    width: 85vw;
    position: absolute;
    top: 75%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.timeout {
    animation: timeout 800ms ease-in-out;
}

.fight {
    animation: fight 1500ms ease-in-out;
}

@keyframes timeout {
    0% { transform: translate(-50%, -50%) scale(1.5, 1.5); opacity: 1.0; }
    100% { transform: translate(-50%, -50%) scale(0, 0); opacity: 0; }
}

@keyframes fight {
    0% { transform: translate(-50%, -50%) scale(0.5, 0.5); opacity: 0; }
    20% { transform: translate(-50%, -50%) scale(1.5, 1.5); opacity: 1.0; }
    100% { transform: translate(-50%, -50%) scale(1.5, 1.5); opacity: 1.0; }
}

@keyframes hide {
    0% { opacity: 1.0; }
    100% { opacity: 0; }
}

.fight-timeout-container {
    opacity: 0;
    animation: hide 400ms ease-in-out;
}

.ore_animate {
    animation: ore_anim 300ms ease-in-out;
}

@keyframes ore_anim {
    0% { transform: translate(-5px, 0%); }
    25% { transform: translate(5px, 0%); }
    50% { transform: translate(-5px, 0%); }
    75% { transform: translate(5px, 0%); }
    100% { transform: translate(-5px, 0%); }
}

.ore_shapes {
    position: absolute;
    width: 10px; height: 10px;
    transform: rotate(0deg);
    animation: 300ms bang ease-out;
}

.cobblestone_shapes {
    box-shadow: -200px -125px #aea29f,-250px 0px #aea29f,-200px 125px #aea29f,200px -125px #aea29f,250px 0px #aea29f,200px 125px #aea29f,-125px -200px #aea29f,0px -250px #aea29f,125px -200px #aea29f,-125px 200px #aea29f,0px 250px #aea29f,125px 200px #aea29f;
}

.coal_shapes {
