}

.iron_shapes {
    box-shadow: -200px -125px #afbec4,-250px 0px #afbec4,-200px 125px #afbec4,200px -125px #afbec4,250px 0px #afbec4,200px 125px #afbec4,-125px -200px #afbec4,0px -250px #afbec4,125px -200px #afbec4,-125px 200px #afbec4,0px 250px #afbec4,125px 200px #afbec4;
