.emerald_shapes {
    box-shadow: -200px -125px #4db5ab,-250px 0px #4db5ab,-200px 125px #4db5ab,200px -125px #4db5ab,250px 0px #4db5ab,200px 125px #4db5ab,-125px -200px #4db5ab,0px -250px #4db5ab,125px -200px #4db5ab,-125px 200px #4db5ab,0px 250px #4db5ab,125px 200px #4db5ab;
