.gold_shapes {
    box-shadow: -200px -125px #ffd64e,-250px 0px #ffd64e,-200px 125px #ffd64e,200px -125px #ffd64e,250px 0px #ffd64e,200px 125px #ffd64e,-125px -200px #ffd64e,0px -250px #ffd64e,125px -200px #ffd64e,-125px 200px #ffd64e,0px 250px #ffd64e,125px 200px #ffd64e;
