.container {
    width: 20vw;
    height: 40px;
    background: rgba(97,144,232,1);
    position: relative;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container:before {
    content: "";
    position: absolute;
    left: 20vw;
    width: 40px;
    height: 40px;
    background: rgba(97,144,232,1);
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
}

.container-dark {
    width: 20vw;
    height: 40px;
    background: rgba(98,100,102,1);
    position: relative;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container-dark:before {
    content: "";
    position: absolute;
    left: 20vw;
    width: 40px;
    height: 40px;
    background: rgba(98,100,102,1);
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
}